<template>
  <div v-if="reload">
    <el-container>
      <el-header>{{ APPName }}<i class="el-icon-milk-tea" /></el-header>
      <el-container>
        <el-aside
          style="width: 100px"
          :style="{ background: bg_sw ? '#fff' : '#333' }"
          ><el-col>
            <el-menu
              default-active="1"
              class="el-menu-vertical-demo"
              background-color="#00000000"
              text-color="#000"
              active-text-color="#689fd2"
            >
              <el-row id="Sun">
                <el-button
                  type="info"
                  icon="el-icon-ice-cream-square"
                  circle
                  @click="bg_sw = !bg_sw"
                ></el-button>
              </el-row>
              <el-menu-item
                index="1"
                @click="
                  data_type = 'recommend';
                  item = 1;
                "
              >
                <i class="el-icon-menu"></i>
                <span slot="title">开始</span>
              </el-menu-item>
              <el-menu-item
                index="2"
                @click="
                  data_type = 'update';
                  item = 2;
                "
              >
                <i class="el-icon-time"></i>
                <span slot="title">更新</span>
              </el-menu-item>
              <el-menu-item index="3" @click="item = 3">
                <i class="el-icon-cold-drink"></i>
                <span slot="title">收藏</span>
              </el-menu-item>
              <el-menu-item index="4" @click="item = 4">
                <i class="el-icon-setting"></i>
                <span slot="title">设置</span>
              </el-menu-item>
            </el-menu>
          </el-col>
        </el-aside>
        <el-main :style="{ background: bg_sw ? '#fff' : '#333' }">
          <transition name="el-fade-in-linear">
            <Love v-if="item == 3" />
            <Data
              v-if="item == 1 || item == 2"
              :Data_type="data_type"
              :key="item"
            />
            <Setting v-if="item == 4" />
          </transition>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Data from "./Data.vue";
import Love from "./love.vue";
import Setting from "./Setting.vue";

export default {
  name: "Home_M",
  data() {
    return {
      APPName: "白鷺茶室",
      data_type: "recommend",
      item: 1,
      bg_sw: true,
      reload: true,
    };
  },
  components: {
    Data,
    Love,
    Setting,
  },
};
</script>

<style lang="scss" scoped>
.el-header {
  background-color: #72bdfd;
  color: #2b597388;
  font-weight: bold;
  line-height: 60px;
  font-size: 30px;
  letter-spacing: 15px;
  text-align: center;
  position: relative;
  font-style: italic;
}

.el-aside {
  overflow: hidden;
  text-align: center;
  line-height: 200px;

  display: block;
  position: absolute;
}
.el-menu-vertical-demo {
  height: calc(100vh - 60px);
}

.el-main {
  left: 100px;
  right: 0;
  top: 60px;
  bottom: 0;
  position: absolute;
  overflow-y: scroll;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
#Sun {
  width: 80px;
  height: 150px;
  position: absolute;
  left: 0px;
  bottom: 0px;
}
</style>