<template >
  <el-row>
    <el-col id="setting" :span="10">
      <transition-group name="el-fade-in-linear">
        <el-card :body-style="{ padding: '0px' }" :key="0">
          <el-col v-for="(item, index) in acg_type" :key="index">
            <el-switch v-model="type_sw[index]" @change="save_data">
            </el-switch>
            <span>隐藏</span>
            <span :style="{ color: acg_colos[index] }">{{ item }}</span>
          </el-col>
        </el-card>
        <el-card :body-style="{ padding: '0px' }" :key="1">
          <el-button
            type="primary"
            icon="el-icon-download"
            @click="put_outJson"
          >
            导出数据</el-button
          >
          <el-button type="primary" icon="el-icon-upload2" @click="put_indata">
            导入数据</el-button
          >
        </el-card>
        <el-card :body-style="{ padding: '0px' }" :key="2">
          <el-button type="danger" icon="el-icon-delete" @click="deleteAll">
            清除所有</el-button
          >
        </el-card>
      </transition-group>
    </el-col>
  </el-row>
</template>

<script>
import FileSaver from "file-saver";
export default {
  name: "Setting_page",

  data() {
    return {
      reload: true,
      acg_colos: [
        "#e62d73",
        "#0089ff",
        "#b3a700",
        "#19ffa8",
        "#8600cc",
        "#00024d",
      ],
      acg_type: ["喜欢", "准备看", "没看完", "已看完", "不想看", "黑名单"],
      type_sw: [false, false, false, false, false, false],
    };
  },

  mounted() {
    if (window.localStorage.getItem("type_sw") != null)
      this.type_sw = JSON.parse(window.localStorage.getItem("type_sw"));
  },

  methods: {
    reload_() {
      this.reload = false;
      this.$nextTick(() => {
        this.reload = true;
      });
    },
    save_data() {
      window.localStorage.setItem("type_sw", JSON.stringify(this.type_sw));
    },
    deleteAll() {
      this.$confirm("此操作将永久清除所有内容, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          window.localStorage.clear();
          location.reload();
          this.$message({
            type: "success",
            message: "删除成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    put_outJson() {
      let max_length = window.localStorage.length;
      var data = [];

      for (let index = 0; index < max_length; index++) {
        let key = window.localStorage.key(index);
        if (key == "type_sw" || key == "bg") continue;
        data = data.concat(JSON.parse(window.localStorage.getItem(key)));
      }
      data = JSON.stringify(data);
      var blob = new Blob([data], { type: "application/json" });
      FileSaver.saveAs(blob, `备份.json`);
    },
    put_indata() {},
  },
};
</script>

<style lang="scss" scoped>
#setting {
  text-align: left;
  line-height: 30px;
}
.el-card {
  padding: 25px;
}
</style>