<template>
  <div>
    <el-radio-group v-model="radio">
      <el-radio
        @change="sendmsg"
        class="radio"
        v-for="(text, index) in acg_type"
        :label="index"
        :key="index"
        >{{ text }}</el-radio
      >
    </el-radio-group>
  </div>
</template>

<script>
export default {
  name: "CheckTeyp",

  data() {
    return {
      acg_type: ["喜欢", "准备看", "没看完", "已看完", "不想看", "黑名单"],
      radio: 0,
    };
  },

  mounted() {},

  methods: {
    sendmsg() {
      this.$emit("get_type", this.radio);
    },
  },
};
</script>

<style lang="scss" scoped>
div {
  margin-bottom: 5px;
}
</style>